
import { defineComponent, onMounted, ref } from "vue";

import {
  IonButtons,
  IonContent,
  IonHeader,
  IonMenuButton,
  IonPage,
  IonTitle,
  IonToolbar,
  IonCard,
  IonCardHeader,
  IonCardContent,
  IonCardSubtitle,
  IonCardTitle,
  IonItem,
  IonLabel,
  IonList,
  IonButton,
  IonInput,
} from "@ionic/vue";

import { useStore, store, ACTIONS } from "../store";
import { useI18n } from "vue-i18n";
export default defineComponent({
  name: "Home",
  components: {
    IonButtons,
    IonContent,
    IonHeader,
    IonMenuButton,
    IonPage,
    IonTitle,
    IonToolbar,
    IonCard,
    IonCardHeader,
    IonCardContent,
    IonCardSubtitle,
    IonCardTitle,
    IonItem,
    IonLabel,
    IonList,
    IonButton,
    IonInput,
  },
  setup() {
    const { t, locale, mergeLocaleMessage } = useI18n();

    mergeLocaleMessage("en", {
      story: {
        title: "Join an adventure!",
        enterCode: "Enter your party code",
        placeholder: "party code",
        join: "Join the party",
      },
    });
    mergeLocaleMessage("de", {
      story: {
        title: "Nimm an einem Spiel Teil!",
        enterCode: "Gib Deinen Code ein",
        placeholder: "Dein Code",
        join: "Absenden",
      },
    });

    const store = useStore();
    const dinnerCode = ref("");
    const dinnerCodeError = ref("");
    const joinDinner = () => {
      store
        .dispatch(ACTIONS.SET_STATE_ID, dinnerCode.value)
        .then(() => (dinnerCodeError.value = ""))
        .catch((error) => {
          console.log(error);
          dinnerCodeError.value = "unbekannter Dinner Code";
        });
    };
    return { store, dinnerCode, dinnerCodeError, joinDinner, t, locale };
  },
});
